.top-bar {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    width: 100%;
    height: 10%;
    max-height: 50px;
    background-color: rgb(50, 50, 50);
}

.pattern-menu {
    position: fixed;
    z-index: 100;
    width: min(75vw, 75vh);
    height: min(75vw, 75vh);
    left: 0;
    top: 0;
    aspect-ratio: 1;
    resize: both;
    display: flex;
    flex-direction: column;
    align-items: center;
    /* justify-content: center; */

    background-color: rgb(50, 50, 50);
    border-bottom: 2px solid black;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
    color: white;
}

.pattern-area {
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
}

.pattern-display {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 10px;
    max-width: min(75vw, 75vh);
    max-height: min(75vw, 75vh); 
}

.pattern-display .game-board {
    width: 50%;
    aspect-ratio: 1;
}

.pattern-display-information {
    width: 50%;
    aspect-ratio: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.pattern-display-information > div {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}